import React from "react";

const AnalyticsByConsent = ({ gtagId }: { gtagId: string }) => {
  return (
    <>
      <script
        dangerouslySetInnerHTML={{
          __html: `
          window.dataLayer = window.dataLayer || [];
          function gtag() {
            dataLayer.push(arguments);
          }
          gtag('consent', 'default', {
            'ad_storage': 'denied',
            'ad_user_data': 'denied',
            'ad_personalization': 'denied',
            'analytics_storage': 'denied'
          });`,
        }}
      />
      {/* Google tag (gtag.js) */}
      <script
        async
        src={`https://www.googletagmanager.com/gtag/js?id=${gtagId}`}
      />
      <script
        dangerouslySetInnerHTML={{
          __html: `
            window.dataLayer = window.dataLayer || [];
            function gtag() {
              dataLayer.push(arguments);
            }
            gtag('js', new Date());

            gtag('config', '${gtagId}', {
              "debug_mode":true,
              "anonymize_ip":false
            });`,
        }}
      />
      <script
        dangerouslySetInnerHTML={{
          __html: `
          var _hsp = (window._hsp = window._hsp || []);
          _hsp.push(["addPrivacyConsentListener", function (consent) {
            console.log("consent listener fires");
            if (consent?.categories?.analytics) {
              console.log("got analytics consent");
              window.clarity('consent');

              gtag('consent', 'update', {
                'analytics_storage': 'granted'
              });

            } else {
              console.log("withdraw analytics consent");
              window.clarity('consent', false); 

              gtag('consent', 'update', {
                'analytics_storage': 'denied'
              });
              
            }

            if (consent?.categories?.advertisement) {
              console.log("got advertisement consent");
              gtag('consent', 'update', {
                'ad_storage': 'granted',
                'ad_user_data': 'granted',
                'ad_personalization': 'granted'
              });
            } else {
              console.log("withdraw advertisement consent");
              gtag('consent', 'update', {
                'ad_storage': 'denied',
                'ad_user_data': 'denied',
                'ad_personalization': 'denied'
              });
              
            }
          }]);`,
        }}
      />
      {/* <script
        dangerouslySetInnerHTML={{
          __html: `
          var _hsp = (window._hsp = window._hsp || []);
          _hsp.push(["addPrivacyConsentListener", function (consent) {
            console.log("consent listener fires");
            if (consent?.categories?.analytics) {
              window.clarity('consent');

              window.dataLayer = window.dataLayer || [];
              function gtag(){dataLayer.push(arguments);}
              gtag('js', new Date());

              gtag('config', '${gtagId}', {
                "debug_mode":true,
                "anonymize_ip":false
              });
            } else {
              window.clarity('consent', false); 
            }
          }]);`,
        }}
      /> */}
    </>
  );
};

export default AnalyticsByConsent;
